<template>
  <div class="container">
    <div class="one">
      <el-row :gutter="20" class="el-row1">
        <el-col :span="6">
          <div class="item">
            <h2>开始时间</h2>
            <el-date-picker
              v-model="begin_at"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期">
            </el-date-picker>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="item">
            <h2>结束时间</h2>
            <el-date-picker
              v-model="end_at"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期">
            </el-date-picker>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="item">
            <h2>入账币种</h2>
            <el-select v-model="input_symbol" placeholder="请选择">
              <el-option
                v-for="item in coins"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="item">
            <h2>出账币种</h2>
            <el-select v-model="output_symbol" placeholder="请选择">
              <el-option
                v-for="item in coins"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="20" class="el-row1">
        <el-col :span="6">
          <div class="item">
            <h2>入账地址</h2>
            <el-select v-model="input_wallet_id" placeholder="请选择">
              <el-option
                v-for="item in coins"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="item">
            <h2>出账地址</h2>
            <el-select v-model="output_wallet_id" placeholder="请选择">
              <el-option
                v-for="item in coins"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="item">
            <h2>担保费</h2>
            <el-select v-model="output_esscow_fee" placeholder="请选择">
              <el-option
                v-for="item in fees"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="item">
            <h2>手续费</h2>
            <el-select v-model="output_fee" placeholder="请选择">
              <el-option
                v-for="item in fees"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="20" class="el-row1">
        <el-col :span="6">
          <div class="item">
            <h2>交易类型</h2>
            <el-select v-model="dealType" placeholder="请选择">
              <el-option
                v-for="item in dealTypes"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="item">
            <h2>交易员</h2>
            <!--<el-select v-model="trader_id" placeholder="请选择">-->
              <!--<el-option-->
                <!--v-for="item in tranderList"-->
                <!--:key="item.value"-->
                <!--:label="item.label"-->
                <!--:value="item.value">-->
              <!--</el-option>-->
            <!--</el-select>-->
            <el-select v-model="trander_id" filterable clearable placeholder="请选择">
              <el-option-group
                v-for="group in tranderList"
                :key="group.label"
                :label="group.label">
                <el-option
                  v-for="item in group.options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-option-group>
            </el-select>
          </div>
        </el-col>
        <el-col :span="6">
          <el-button type="primary" @click="search">查询</el-button>
        </el-col>
      </el-row>

    </div>
    <div class="one" style="margin-top: 10px;">
      <div class="section section1">
        <el-row :gutter="20">
          <el-col :span="3">
            <p>币种</p>
          </el-col>
          <el-col :span="7">
            <p>入账</p>
          </el-col>
          <el-col :span="6">
            <p>出账</p>
          </el-col>
        </el-row>
      </div>
      <div v-for="item in subtotals" class="section">
        <el-row :gutter="20">
          <el-col :span="3">
            <p>{{item.symbol}}</p>
          </el-col>
          <el-col :span="7">
            <h3>{{item.input_amount}}</h3>
          </el-col>
          <el-col :span="6">
            <h4>{{item.output_amount}}</h4>
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="one" style="margin-top: 10px;">
      <div class="action action1">
        <el-row :gutter="20">
          <el-col :span="4">
            <p>日期</p>
          </el-col>
          <el-col :span="3">
            <p>交易类型</p>
          </el-col>
          <el-col :span="3">
            <p>入账金额</p>
          </el-col>
          <el-col :span="2">
            <p>入账地址</p>
          </el-col>
          <el-col :span="3">
            <p>出账金额</p>
          </el-col>
          <el-col :span="2">
            <p>出账地址</p>
          </el-col>
          <el-col :span="2">
            <p>担保费</p>
          </el-col>
          <el-col :span="2">
            <p>手续费</p>
          </el-col>
          <el-col :span="2">
            <p>操作者</p>
          </el-col>
        </el-row>
      </div>
      <div v-for="(item,index) in orders" :class="index%2 === 0?'bg1':'bg2'">
        <el-row :gutter="20" class="action">
          <el-col :span="4">
            <p>{{item.created_text}}</p>
          </el-col>
          <el-col :span="3">
            <p>{{item.type}}</p>
          </el-col>
          <el-col :span="3">
            <p>{{item.input_amount}}</p>
          </el-col>
          <el-col :span="2">
            <p>{{item.input_wallet_name}}</p>
          </el-col>
          <el-col :span="3">
            <p>{{item.output_amount}}</p>
          </el-col>
          <el-col :span="2">
            <p>{{item.output_wallet_name}}</p>
          </el-col>
          <el-col :span="2">
            <p>{{item.output_esscow_fee}}</p>
          </el-col>
          <el-col :span="2">
            <p>{{item.output_fee}}</p>
          </el-col>
          <el-col :span="2">
            <p>{{item.trader_name}}</p>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>
<script>
  import { httpGet } from '../../utils/util'
  import { mapState } from 'vuex'
  export default {
    data () {
      return {
        begin_at:'',
        end_at:'',
        input_symbol:'',
        output_symbol:'',

        input_wallet_id:'',
        output_wallet_id:'',
        output_fee:'',
        output_esscow_fee:'',

        coins: [],
        fees: [{
          value: '',
          label: '全部'
        }, {
          value: '1',
          label: '无'
        }, {
          value: '-1',
          label: '有'
        }],
        tranderList: [],
        trander_id: '',
        dealTypes: [{
          value: '',
          label: '全部'
        }, {
          value: '0',
          label: '买币'
        }, {
          value: '1',
          label: '卖币'
        }, {
          value: '2',
          label: '拆借'
        }, {
          value: '3',
          label: '还款'
        }, {
          value: '4',
          label: '代买'
        }, {
          value: '5',
          label: '代卖'
        },{
          value: '6',
          label: '申请备用金'
        },{
          value: '7',
          label: '返还备用金'
        },{
          value: '10',
          label: '自我转账',
        },{
          value: '11',
          label: '手续费'
        }],
        dealType: '',
        orders: [],

        subtotals:[]
      }
    },
    computed: {
      ...mapState(["userInfo"])
    },
    mounted () {
      this.getSymbols();
      this.TraderObject();
      this.search();
    },
    methods:{
      search () {
        this.subtotalsSearch();
        this.ordersSearch()
      },
      ordersSearch () {
        const that = this
        httpGet('/orders/search', {
          begin_at: that.begin_at,
          end_at: that.end_at,
          type: that.dealType,
          input_symbol: that.input_symbol,
          output_symbol: that.output_symbol,
          trader_id: that.trander_id,
//          input_wallet_id:that.input_wallet_id,
//          output_wallet_id:that.output_wallet_id,
          output_fee: that.output_fee,
          output_esscow_fee: that.output_esscow_fee,
          sid: that.userInfo.sid
        }, res => {
            if (res.data.error_code === 0) {
                that.orders = res.data.orders
            }
        })
      },
      subtotalsSearch () {
        const that = this
        httpGet('/subtotals/search', {
          //交易员的trader_id，非财务
          trader_id: that.trander_id,
          start_time: that.begin_at,
          end_time: that.end_at,
          sid: that.userInfo.sid
        }, res => {
          if (res.data.error_code === 0) {
            that.subtotals = res.data.all_wallet_price
          }
        })
      },
      //获取货币种类
      getSymbols() {
        var that = this;
        httpGet("/symbols/symbols",{},function (res) {
          if(res.data.error_code == 0) {
            that.coins = res.data.symbols;
          }
          var item = {
            value: '',
            label: '全部'
          }
          that.coins.unshift(item);
        })
      },
      //获取交易者列表
      TraderObject() {
        var that = this;
        httpGet("/traders/profile",{
          type: 1,
          sid: that.userInfo.sid
        },function (res) {
//          console.log(res);
          if (res.data.error_code == 0) {
            var traders = res.data.trader;
            var obj = {};
            var arr = [];
            for(var key in traders){
              var item = {};
              item.value = traders[key].id;
              item.label = traders[key].name;
              arr.push(item);
            }
            obj.label = "内部人员";
            obj.options = arr;
            that.tranderList.push(obj);
          }
        });
        httpGet("/traders/profile",{
          type: 2,
          sid: that.userInfo.sid
        },function (res) {
          if (res.data.error_code == 0) {
            var traders = res.data.trader;
            var obj = {};
            var arr = [];
            for(var key in traders){
              var item = {};
              item.value = traders[key].id;
              item.label = traders[key].name;
              arr.push(item);
            }
            obj.label = "外部人员";
            obj.options = arr;
            that.tranderList.push(obj);
          }
        });
      },
    }
  }
</script>
<style scoped>
  .one{
    border-bottom: 1px solid #ddd;
    margin-bottom: 20px;
  }
  .el-row1{
    margin-bottom: 20px;
  }
  .el-col{
    /*border: 1px solid red;*/
  }
  .item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-right: 20px;
  }
  .item h2{
    font-size:14px;
    color: #333;
    font-weight: 300;
    width: 100px;
    text-align: left;
    /*border: 1px solid yellow;*/
  }
  .section{
    margin-bottom: 10px;
    /*border: 1px solid red;*/
  }
  .section p{
    font-size:16px;
    color: #333;
    font-weight: 300;
    text-align: center;
  }
  .section h3{
    font-size:16px;
    color: blue;
    font-weight: 100;
    text-align: center;
  }
  .section h4{
    font-size:14px;
    color: red;
    font-weight: 100;
    text-align: center;
  }
  .action{
    height: 40px;
    line-height: 40px;
  }
  .action p{
    text-align: center;
  }
  .action1{
    background-color: #00C6FF;
  }
  .bg1{
    background-color: darkgreen;
  }
  .bg2{
    background-color: indianred;
  }
</style>



